            </label>
            <label class="choice">
              <input type="radio" :name="`attn-num-${idx}`" value="4" v-model="responses[idx].attnNumOptions" />
              4
            </label>
            <label class="choice">
              <input type="radio" :name="`attn-num-${idx}`" value="6" v-model="responses[idx].attnNumOptions" />
              6
            </label>
          </div>
        </InstructionScreen>
      </template>

      <!-- Post-test + submit -->
      <PostTestScreen />
      <SubmitResultsScreen />
    </template>
  </Experiment>
</template>

<script>
import Papa from "papaparse";

/** --- deterministic random helpers (stable per participant) --- **/
function hashStringToUint32(str) {
  // simple, stable hash
  let h = 2166136261;
  for (let i = 0; i < str.length; i++) {
    h ^= str.charCodeAt(i);
    h = Math.imul(h, 16777619);
  }
  return h >>> 0;
}

function mulberry32(seed) {
  return function () {
    let t = (seed += 0x6d2b79f5);
    t = Math.imul(t ^ (t >>> 15), t | 1);
    t ^= t + Math.imul(t ^ (t >>> 7), t | 61);
    return ((t ^ (t >>> 14)) >>> 0) / 4294967296;
  };
}

function shuffleInPlace(arr, rand) {
  for (let i = arr.length - 1; i > 0; i--) {
    const j = Math.floor(rand() * (i + 1));
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr;
}

export default {
  name: "App",
  data() {
    return {
      loading: true,
      errorMessage: "",
      trials: [],
      responses: []
    };
  },
  methods: {
    screenTitle(trial) {
      return trial.scenario_title || "Story";
    },
    getParticipantId() {
      const key = "ba_thesis_pid";
      let pid = localStorage.getItem(key);
      if (!pid) {
        pid = `${Date.now()}_${Math.random().toString(16).slice(2)}`;
        localStorage.setItem(key, pid);
      }
      return pid;
    }
  },
  async mounted() {
    try {
      const res = await fetch("stimuli/vignettes.csv");
      if (!res.ok) throw new Error(`Failed to fetch CSV: ${res.status}`);

      const csvText = await res.text();
      const parsed = Papa.parse(csvText, { header: true, skipEmptyLines: true });

      const rows = parsed.data.filter((r) => r && r.scenario_id);
      if (!rows.length) throw new Error("CSV loaded but has no rows with scenario_id.");

      // Group rows by scenario_id
      const byScenario = rows.reduce((acc, row) => {
        (acc[row.scenario_id] ||= []).push(row);
        return acc;
      }, {});

      // Scenario list = your 6 scenarios (match your scenario_id in CSV)
      const scenarioArr = [
        "car_accident",
        "memory_test",
        "theater_interruption",
        "metro_escalator",
        "airport_luggage",
        "apartment_explosion"
      ];

      // Arrays recommended by Michael (length 6 each)
      const distalArr = [
        "natural",
        "non_deliberate",
        "deliberate",
        "natural",
        "non_deliberate",
        "deliberate"
      ];

      const valenceArr = ["positive", "negative", "positive", "negative", "positive", "negative"];

      // Participant-stable randomization
      const pid = this.getParticipantId();
      const rand = mulberry32(hashStringToUint32(pid));

      // Shuffle each independently
      shuffleInPlace(scenarioArr, rand);
      shuffleInPlace(distalArr, rand);
      shuffleInPlace(valenceArr, rand);

      // Build trials in order, then randomize final trial order too (as requested)
      const built = scenarioArr.map((sid, i) => {
        const candidates = byScenario[sid];
        if (!candidates || !candidates.length) throw new Error(`No rows found for scenario_id="${sid}"`);

        const desiredDistal = distalArr[i];
        const desiredValence = valenceArr[i];

        const chosen = candidates.find((r) => r.distal_type === desiredDistal) || candidates[0];

        const outcomeShown =
          desiredValence === "positive" ? chosen.outcome_positive : chosen.outcome_negative;

        return { ...chosen, valence: desiredValence, outcome_shown: outcomeShown };
      });

      // Randomize the order of trials
      shuffleInPlace(built, rand);
      this.trials = built;

      // Response objects
      this.responses = this.trials.map((t) => ({
        scenario_id: t.scenario_id,
        scenario_title: t.scenario_title,
        distal_type: t.distal_type,
        valence: t.valence,
        freeText: "",
        causeChoice: "",
        attnValence: "",
        attnNumOptions: ""
      }));

      console.log("Loaded trials:", this.trials.map((t) => [t.scenario_id, t.distal_type, t.valence]));
    } catch (err) {
      console.error(err);
      this.errorMessage = String(err.message || err);
    } finally {
      this.loading = false;
    }
  }
};
</script>

